import { UserProfile } from '@/types/UserProfile'
import { getRandomFromArr, getEmojis, randomInRange } from '@/utils/utils'
import { UserInfo } from 'firebase'
import bgImages from '@/utils/bgImages'
import allQuotes from '@/utils/quotes'

const socialLinks = {
  instagram: {
    icon: 'fa fa-instagram',
    link: 'https://www.instagram.com/...'
  },
  facebook: {
    icon: 'fa fa-facebook',
    link: 'https://www.facebook.com/...'
  },
  youtube: {
    icon: 'fa fa-youtube',
    link: 'https://youtube.com/c/...'
  },
  spotify: {
    icon: 'fa fa-spotify',
    link: 'https://open.spotify.com/artist/...'
  },
  twitter: {
    icon: 'fa fa-twitter',
    link: ''
  },
  github: {
    icon: 'fa fa-github-alt',
    link: ''
  },
  linkedin: {
    icon: 'fa fa-linkedin',
    link: ''
  }
}

const getRandomQuote = () => {
  const { author, quotes } = getRandomFromArr(allQuotes) as { author: string; quotes: string[] } 
  const quote = getRandomFromArr(quotes) as string
  return `"${quote}"    - ${author}`
}

const newUserProfile = ({ uid, displayName, photoURL }: UserInfo): UserProfile => ({
  userUid: uid,
  displayName,
  coverImg: getRandomFromArr(bgImages) as string,
  photoURL,
  bgColor: '#000000',
  shortBio: `${getEmojis(randomInRange(1, 3))} What/Where ${getEmojis(randomInRange(1, 3))}`,
  longBio: getRandomQuote(),
  socialLinks
})

export default newUserProfile
