import { BaseContentCard } from "@/types/BaseContentCard";

const YOUTUBE_PLAYLIST = /(?:youtu\.be|youtube\.com)(?:.*?)(?:\?|&)list=(.+?)(?:&|$)/
const YOUTUBE_VIDEO_1 = /youtube\.com(?:.*?)(?:\/|\?|&)(?:v=)(.+?)(?:&|$)/
const YOUTUBE_VIDEO_2 = /youtu\.be\/(.+?)(?:\/|\?|&|$)/
const SPOTIFY = /spotify\.com(?:.+?)(playlist|track|album)(?:\/|:)(.+?)(?:\/|:|$)/
const INSTAGRAM_POST = /instagram.com\/(p|tv)\/(.+?)(?:\/|\?|$)/

const parseKnownUrl = (rawUrl: string): Partial<BaseContentCard> => {
    const [,youtubePlaylistId] = rawUrl.match(YOUTUBE_PLAYLIST) || [null, null]
    if (youtubePlaylistId) return {
        type: 'iframe',
        sourceUrl: `https://www.youtube.com/playlist?list=${youtubePlaylistId}`,
        iFrameUrl: `https://www.youtube.com/embed/videoseries?list=${youtubePlaylistId}`
    }

    const [,youtubeVideoId1] = rawUrl.match(YOUTUBE_VIDEO_1) || [null, null]
    if (youtubeVideoId1) return {
        type: 'iframe',
        sourceUrl: `https://youtu.be/${youtubeVideoId1}`,
        iFrameUrl: `https://www.youtube.com/embed/${youtubeVideoId1}`
    }
    const [,youtubeVideoId2] = rawUrl.match(YOUTUBE_VIDEO_2) || [null, null]
    if (youtubeVideoId2) return {
        type: 'iframe',
        sourceUrl: `https://youtu.be/${youtubeVideoId2}`,
        iFrameUrl: `https://www.youtube.com/embed/${youtubeVideoId2}`
    }

    const [,spotiType, spotiId] = rawUrl.match(SPOTIFY) || [null, null, null]
    if (spotiType && spotiId) return {
        type: 'iframe',
        sourceUrl: `https://open.spotify.com/${spotiType}/${spotiId}`,
        iFrameUrl: `https://open.spotify.com/embed/${spotiType}/${spotiId}`
    }

    const [,instagramType, instagramId] = rawUrl.match(INSTAGRAM_POST) || [null, null]
    if (instagramId) return {
        sourceUrl: `https://www.instagram.com/${instagramType}/${instagramId}/?utm_source=ig_web_button_share_sheet`,
    }

    return {}
}

export default parseKnownUrl
