<template>   
    <div v-if="isOwnProfile && !isUploading" class="text-center">
      <div v-if="tryURL.trim().length <= 0">
          <label for="try-image" v-bind:style="`color: ${invertedHex};`" class="btn border m-1 h5">📸 Upload Photo</label><input type="file" id="try-image" @change="handleImageFile" hidden accept="image/*" />
          <label for="try-video" v-bind:style="`color: ${invertedHex};`" class="btn border m-1 h5">🎥 Upload Video</label><input type="file" id="try-video" @change="handleVideoFile" hidden accept="video/*" />
      </div>

      <input type="text" class="col-10 col-sm-8 text-center my-3 p-1" placeholder="Or paste a URL here... (Experimental: Try a YouTube, Spotify or Instagram public link)" v-model="tryURL" />
      <!-- Force next columns to break to new line -->
      <div class="w-100"></div>

      <button v-if="tryURL.trim().length > 0" type="button" class="btn bg-transparent border" @click="tryContentCardUpload()"><h5 v-bind:style="`color: ${invertedHex};`">🌐 Fetch URL</h5></button>
    </div>

    <div class="text-center" v-if="isOwnProfile && isUploading">
        <Spinner />
    </div>
          
    <div class="row align-items-center p-2" v-if="isOwnProfile && activeDocs?.length > 0">
        <h4><i class="fa fa-pencil mr-1"></i></h4>
        <label for="editCardsCheckbox" class="el-switch"><input type="checkbox" name="switch" v-model="editMode" id="editCardsCheckbox">
          <span class="el-switch-style"></span>
        </label>  
    </div>
    <div class="row align-items-center p-2" v-if="!isOwnProfile && activeDocs?.length > 0">
        <h4><i class="fa fa-external-link mr-1"></i></h4>
        <label for="showPublicOptionsCheckbox" class="el-switch"><input type="checkbox" name="switch" v-model="showPublicOptions" id="showPublicOptionsCheckbox">
          <span class="el-switch-style"></span>
        </label>  
    </div>

    <div class="container">
      <div class="row align-items-center mx-sm-3">
        <ContentCard v-for="{postUid, contentCard} in activeDocs" :key="postUid" :contentCard="contentCard" :showPublicOptions="showPublicOptions" :editMode="editMode" :userUid="userUid" :postUid="postUid" :onDelete="() => deleteContentCard(postUid, contentCard?.hostedFileName)" />
      </div>
    </div>

    <div class="row justify-content-center mt-4" v-if="endPage > startPage" v-bind:style="`color: black;`">
      <ul class="pagination bg-secondary">
        <li class="page-item" v-bind:class="currentPage == startPage ? 'active':''" @click="currentPage = startPage">
         <a class="page-link"> {{startPage}}</a>
        </li>
        <li class="page-item" v-bind:class="currentPage == page ? 'active':''" v-for="(page, index) in midPages" :key="index" @click="currentPage = page">
          <a class="page-link">{{page}}</a>
        </li>
        <li class="page-item" v-bind:class="currentPage == endPage ? 'active':''" @click="currentPage = endPage">
          <a class="page-link">{{endPage}}</a>
        </li>
      </ul>
    </div>
</template>

<script lang="ts">
/* eslint-disable @typescript-eslint/no-use-before-define */


import { reactive, toRefs, onMounted, computed } from 'vue'
import { auth, getTimelineCollection, storageRef } from '../firebase.config'
import ContentCard from '@/components/shared/ContentCard'
import paginate from '@/utils/paginate'
import { BaseContentCard } from '@/types/BaseContentCard'
import parseKnownUrl from '@/controllers/KnownUrlsParser'
import deleteContentCard from '@/services/deleteContentCard'
import { FetchUrlMeta } from '@/services/FetchUrlMeta'
import Spinner from '@/components/shared/Spinner'
import { getEmojis, randomInRange } from '@/utils/utils'
import router from '@/router'
import { useToaster } from '@ajmey/toaster/toaster'

const toaster = useToaster({animation: "appear", theme: "default", dismiss: 3000 })

export default {
  components: {
    ContentCard,
    Spinner
  },
  props: {
    userUid: String,
    invertedHex: String
  },
  async setup (props) {
    const PAGE_SIZE = 20
    const isOwnProfile = auth?.currentUser?.uid === props.userUid
     
    const timelineRef = getTimelineCollection(props.userUid)
    
    onMounted(async () => {
      if (isOwnProfile) {
        timelineRef.orderBy('ts', 'desc').onSnapshot((refs) => {
          console.log('timelineRef onSnapshot', { refs })
          // eslint-disable-next-line @typescript-eslint/no-use-before-define
          state.docsRefs = refs.docs
        })
      }
    })

    const activeDocs = computed(() => {
      const { startPage, endPage, startIndex, endIndex } = paginate(state.docsRefs.length, state.currentPage, PAGE_SIZE)
      state.startPage = startPage
      const prev = state.currentPage - 1 >= startPage ? state.currentPage - 1 : 0;
      const next = state.currentPage + 1 <= endPage ? state.currentPage + 1 : 0;
      state.midPages = [prev, state.currentPage, next].filter(p => p > startPage && p < endPage)
      state.endPage = endPage
      return state.docsRefs.slice(startIndex, endIndex + 1).map(d => ({ postUid: d.id, contentCard: d.data() as BaseContentCard }))
    })

    const state = reactive({
      docsRefs: (await timelineRef.orderBy('ts', 'desc').get()).docs,
      editMode: false,
      showPublicOptions: false,
      tryURL: '',
      tryFile: null,
      isUploading: false,
      activeDocs,
      startPage: 1,
      endPage: 1,
      currentPage: 1,
      midPages: [],
    })

    const tryContentCardUpload = async (contentCard?: BaseContentCard): Promise<void> => {
      state.isUploading = true
      let newContentCard: BaseContentCard
      try {
        if (contentCard) {
          newContentCard = contentCard
        } else {
          newContentCard = await parseKnownUrl(state.tryURL.trim())
          if (newContentCard?.type) {
            newContentCard = {
              ...newContentCard,
              ...(await FetchUrlMeta(newContentCard.sourceUrl, newContentCard.type))
            }
          } else {
            newContentCard = {
              ...newContentCard,
              ...(await FetchUrlMeta(state.tryURL.trim()))
            }
          }
        }

        // Default to iframe content
        if (newContentCard.type === 'iframe' && !newContentCard?.iFrameUrl) {
          newContentCard.iFrameUrl = newContentCard.sourceUrl
        }
        // Default to emoji title/desc
        if (!newContentCard?.title?.length > 0) {
          newContentCard.title = getEmojis(randomInRange(1, 3))
        }
        if (!newContentCard?.description?.length > 0) {
          newContentCard.description = getEmojis(randomInRange(1, 3))
        }

        const { currentUser } = auth
        const { uid } = currentUser || {}
        if (!uid) return
        const docRef = getTimelineCollection(uid)
        docRef.add(newContentCard).then(({ id: newPostUid }) => {
          state.tryURL = ''
          console.log('New link posted! 👍', { newPostUid })
          router.push(`/u/${uid}/p/${newPostUid}`)
          state.isUploading = false
        })
        
      } catch ({ code, message }) {
        console.error('addContent Error', { code, message })
        state.isUploading = false
      }
    }

    const handleImageFile = async ({ target }) => {
      state.isUploading = true
      try {
        const { files: [file,] } = target
        const { name, size } = file
        if (size > 1 * 1024 * 1024) { // 1MB
          toaster.failure("Max. size: 1Mb ❌")
          target.value = null
          state.isUploading = false
          return
        }
        
        const hostedFileName = `${new Date().getTime()}_${name}`
        const fileRef = storageRef.child(`${auth?.currentUser?.uid}/${hostedFileName}`)
        const { ref } = await fileRef.put(file)
        const downloadURL = await ref.getDownloadURL()
        target.value = null
        await tryContentCardUpload({
          type: 'image',
          sourceUrl: downloadURL,
          previewImage: downloadURL,
          rawImages: [downloadURL],
          ts: new Date(),
          title: '',
          description: '',
          hostedFileName
        })
       } catch ({ code, message }) {
        console.error('handleImageFile Error', { code, message })
      }
      state.isUploading = false
    }

    const handleVideoFile = async ({ target }) => {
      state.isUploading = true
      try {
        const { files: [file,] } = target
        const { name, size } = file
        if (size > 15 * 1024 * 1024) { // 15MB
          toaster.failure("Max. size: 15Mb ❌")
          target.value = null
          state.isUploading = false
          return
        }

        const hostedFileName = `${new Date().getTime()}_${name}`
        const fileRef = storageRef.child(`${auth?.currentUser?.uid}/${hostedFileName}`)
        const { ref } = await fileRef.put(file)
        const downloadURL = await ref.getDownloadURL()
        target.value = null
        await tryContentCardUpload({
          type: 'video',
          sourceUrl: downloadURL,
          previewVideo: downloadURL,
          rawVideos: [downloadURL],
          ts: new Date(),
          title: '',
          description: '',
          hostedFileName
        })
       } catch ({ code, message }) {
        console.error('handleVideoFile Error', { code, message })
      }
      state.isUploading = false
    }

    return {
        ...toRefs(state),
        isOwnProfile,
        deleteContentCard,
        tryContentCardUpload,
        handleImageFile,
        handleVideoFile,
    }
  }
}
</script>
