<template>
    <usePatchMeta :title="profileData.displayName" :description="profileData.shortBio" :previewUrl="profileData.photoURL" />
    <div class="m-2 text-right">
      <button v-if="isLoggedIn" type="button" class="btn btn-light border m-1" @click="signOut">Close Session  <i class="fa fa-sign-out"></i></button>
      <button v-if="!isLoggedIn" type="button" class="btn btn-light border m-1" @click="signIn">Sign In  <i class="fa fa-sign-in"></i></button>
      <button v-if="isOwnProfile && !editMode" type="button" class="btn btn-light border m-1" @click="editColorMode = !editColorMode">{{editColorMode ? 'Hide Color-Picker' : 'Color-Picker'}} <i class="fa fa-paint-brush"></i></button>
      <button type="button" class="btn btn-light border m-1" @click="showShareProfile = !showShareProfile">{{showShareProfile ? 'Hide Bio Share' : 'Bio Share'}} <i class="fa fa-external-link"></i></button>
    </div>

    <div v-show="showShareProfile" id="share-box" class="mx-3 row justify-content-center align-items-center text-center pt-3">
          <div class="col-3" id="qr-box"></div>
          <div class="col-6 d-none d-sm-block">
            <h3 class="text-left mb-4">Share this Bio!</h3>
            <p><strong>🌐 Regular Link</strong> | <a style="text-decoration: none;" :href="shareLinks.regular">https://instab.io{{shareLinks.regular}}</a></p>
            <hr />
            <p><strong>💜 Insta</strong> | <a style="text-decoration: none;" :href="shareLinks.insta">https://instab.io{{shareLinks.insta}}</a> <small>(no <strong>#</strong> 🤷🏻)</small></p>
          </div>
          <div class="col-9 d-sm-none mt-4">
            <p><strong><a style="text-decoration: none;" :href="shareLinks.regular">🌐 Regular Link</a></strong></p>
            <hr class="px-5 mx-5" />
            <p><strong><a style="text-decoration: none;" :href="shareLinks.insta">💜 Insta Link</a></strong></p>
          </div>
    </div>

    <div v-show="isOwnProfile && editColorMode && !editMode" class="row justify-content-center p-4">
      <div id="color-picker"></div>
    </div>

    <div class="container-fluid pb-3" v-bind:style="`background-color: ${updateColor || profileData.bgColor}; color: ${invertedHex}`">
        
      <div class="row align-items-center p-2" v-if="isOwnProfile">
          <h4><i class="fa fa-pencil mr-1"></i></h4>
          <label for="editModeCheckbox" class="el-switch"><input type="checkbox" name="switch" v-model="editMode" id="editModeCheckbox">
            <span class="el-switch-style"></span>
          </label>  
      </div>

      <EditableImgInput :field="'coverImg'" :value="profileData.coverImg" :editMode="editMode" class="cover-img" />
      <EditableImgInput :field="'photoURL'" :value="profileData.photoURL" :editMode="editMode" v-bind:style="editMode ? '':'margin-top: -55px;'" class="profile-img" :border-color="invertedHex"/>
                  
      <h1><EditableTextInput :field="'displayName'" :value="profileData.displayName" :editMode="editMode" class="text-center"/></h1>
      <EditableTextInput :field="'shortBio'" :value="profileData.shortBio" :editMode="editMode" class="text-center" />
      
      <div class="text-center">
        <h2 v-if="!editMode">
          <span v-for="({ icon, link, enabled}, key) in socialLinksToShow" :key="key">
            <a v-if="enabled" v-bind:style="`color: ${invertedHex};`" target="_blank" :href="link" class="m-2">
              <i :class="icon"></i>
            </a>
          </span>
        </h2>
        <h2 v-if="editMode">
          <a style="cursor: pointer;" class="m-2" v-bind:style="`color: ${enabled ? invertedHex : 'darkgrey'};`" @click="handleSocialLinkClick(key, link)" v-for="({ icon, link, enabled}, key) in socialLinksToShow" :key="key">
            <i :class="icon"></i>
          </a>
        </h2>
        <input v-if="editMode && editSocialLinkMode" type="text" class="text-center m-1 p-1 col-6" v-model="trySocialLink" @blur="({ target: { value } }) => updateSocialLink(socialLinkInputFor, value, socialLinksToShow[socialLinkInputFor].link)" :placeholder="`Paste ${socialLinkInputFor} URL`" />
      </div>

      <hr v-bind:style="`background-color: ${invertedHex};`" class="m-3" />

      <CollapsibleContent v-if="profileData?.longBio?.trim().length > 0 || editMode">
        <template v-slot:collapsible-title>
           Bio
        </template>
        <template v-slot:collapsible-content>
          <EditableTextInput :field="'longBio'" :value="profileData.longBio" :editMode="editMode" v-bind:class="profileData.longBio?.trim().length < 140 ? 'h4 text-center' : 'text-justify'" />
        </template>
      </CollapsibleContent>

      <hr v-if="profileData?.longBio?.trim().length > 0 || editMode" v-bind:style="`background-color: ${invertedHex};`" class="m-3" />
      
      <Suspense>
        <ProfileTimeline :userUid="userUid" :invertedHex="invertedHex" />
      </Suspense>
    </div>
</template>

<script lang="ts">
/* eslint-disable @typescript-eslint/no-use-before-define */

import { auth, profilesCollection } from '@/firebase.config'
import { reactive, toRefs, computed, onMounted } from 'vue'
import router from '@/router'
import { UserProfile } from '@/types/UserProfile'
import EditableTextInput from '@/components/shared/EditableTextInput'
import EditableImgInput from '@/components/shared/EditableImgInput'
import CollapsibleContent from '@/components/shared/CollapsibleContent'
import ProfileTimeline from '@/components/ProfileTimeline'
import updateProfileValue from '@/services/updateProfileValue'
import { invertHex } from '@/utils/utils'
import usePatchMeta from '@/components/shared/usePatchMeta'

const updateSocialLink = (site: string, link: string, prevLink: string) => {
  updateProfileValue(`socialLinks.${site}.link`, link, prevLink)
}

export default {
  components: {
    EditableTextInput,
    EditableImgInput,
    ProfileTimeline,
    CollapsibleContent,
    usePatchMeta
  },
  props: {
    userUid: String,
  },
  async setup (props) {
    const isOwnProfile = auth?.currentUser?.uid === props.userUid
    const shareLinks = {
      regular: `/#/u/${props.userUid}`,
      insta: `/user?u=${props.userUid}`,
    }

    const socialLinksToShow = computed(() => {
      const filled = {}
      Object.keys(reactiveFields.profileData.socialLinks).forEach(key => {
        const { link = '', icon } = reactiveFields?.profileData?.socialLinks[key]
        const enabled = link?.length > 0
        filled[key] = {
          link,
          icon,
          enabled,
        }
      })
      return filled
    })
    const invertedHex = computed(() => invertHex(reactiveFields.updateColor || reactiveFields.profileData.bgColor))
    
    const signOut = () => auth.signOut()
    const signIn = () => router.push('/')

    const handleSocialLinkClick = (site: string, currentLink = '') => {
      reactiveFields.editSocialLinkMode = !reactiveFields.editSocialLinkMode
      if (!reactiveFields.editSocialLinkMode) {
        reactiveFields.socialLinkInputFor = ''
        reactiveFields.trySocialLink = ''
        return
      }
      reactiveFields.socialLinkInputFor = site
      reactiveFields.trySocialLink = currentLink
    }

    onMounted(async () => {
      const parent = document.getElementById('color-picker')
      parent.innerHTML = ''
      // eslint-disable-next-line no-undef
      const picker = new Picker({ parent, alpha: false, popup: false, color: reactiveFields.updateColor || reactiveFields.profileData.bgColor }) // https://vanilla-picker.js.org/gen/Picker.html#Picker__anchor

      picker.onChange = ({hex: hexChange}) => {
        // console.log({ hexChange })
        reactiveFields.updateColor = hexChange.slice(0,7)
      } 
      picker.onDone = ({hex: hexDone}) => {
        // console.log({ hexDone })
        updateProfileValue('bgColor', hexDone.slice(0,7), reactiveFields.profileData.bgColor)
        reactiveFields.editColorMode = false
      }
      picker.onClose = () => {
        // console.log({ hexClosed })
        reactiveFields.updateColor = ''
      }
      picker.onOpen = ({hex: hexOpened}) => {
        reactiveFields.updateColor = hexOpened.slice(0,7)
      }

      // eslint-disable-next-line no-undef
      const newQR = new VanillaQR({
        url: shareLinks.regular,
        // colorLight: invertedHex.value || 'white',
        // colorDark: reactiveFields?.updateColor || reactiveFields?.profileData?.bgColor || 'black',
        noBorder: true
      });
      const qrBox = document.getElementById('qr-box')
      // if (!qrBox) return
      qrBox.innerHTML = ''  
      qrBox.appendChild(newQR.domElement)
      
      //Keep updating user profile template for own user profile
      if (isOwnProfile) {
        userRef.onSnapshot((userDoc) => {
          const { displayName, photoURL, coverImg, shortBio, longBio, bgColor, socialLinks } = userDoc.data() as UserProfile
          reactiveFields.profileData = { displayName, photoURL, coverImg, shortBio, longBio, bgColor, socialLinks }
          console.log('Profile onSnapshot', { displayName, photoURL, coverImg, shortBio, longBio, bgColor, socialLinks })
        })
      }
    })

    const userRef = profilesCollection.doc(props.userUid)
    const userDoc = await userRef.get()
    if (!userDoc?.exists) {
      console.log('Profile not found, TODO 404')
      router.replace('/404')
      return
    }

    const { displayName, photoURL, coverImg, shortBio, longBio, bgColor, socialLinks } = userDoc.data() as UserProfile
    console.log('Profile get', { displayName, photoURL, coverImg, shortBio, longBio, bgColor, socialLinks })

    const isLoggedIn = auth && auth?.currentUser
    const reactiveFields = reactive({
      isLoggedIn,
      editMode: false,
      editSocialLinkMode: false,
      editColorMode: false,
      socialLinkInputFor: '',
      trySocialLink: '',
      updateColor: '',
      showShareProfile: false,
      profileData: {
        displayName, photoURL, coverImg, shortBio, longBio, bgColor, socialLinks,
      }
    })

    return {
      ...toRefs(reactiveFields),
      isOwnProfile,
      signIn,
      signOut,
      invertedHex,
      handleSocialLinkClick,
      updateSocialLink,
      socialLinksToShow,
      shareLinks
    }
  },
}
</script>

<style lang="css">
/* Custom CSS here */

#qr-box canvas {
  width: 110px;
  height: 110px;
}

.cover-img img {
  height: 250px;
  object-fit: cover;
}

.profile-img img {
  height: 110px;
  width: 110px;
  border-radius: 50%;
  object-fit: cover;
}

</style>
