import { RouteRecordRaw, createRouter, createWebHashHistory } from 'vue-router'
import { auth, profilesCollection } from '../firebase.config'
import newUserProfile from '@/controllers/NewUserProfile'
import Error404 from '@/views/Error404.vue'
import Login from '@/views/Login.vue'
import Post from '@/views/Post.vue'
import Profile from '@/views/Profile.vue'

const router = createRouter({
  history: createWebHashHistory(),
  routes: []
})

const routes: Array<RouteRecordRaw> = [
  {
    path: '/404',
    component: Error404, 
  },
  {
    path: '/',
    component: Login,
    meta: { toProfileIfLoggedIn: true }
  },
  {
    path: '/u/:userUid/p/:postUid',
    props: true,
    component: Post,
  },
  {
    path: '/u/:userUid',
    props: true,
    component: Profile,
    beforeEnter: async (to, from, next) => {
      const { currentUser } = auth
      const isOwnProfile = currentUser?.uid === to.params.userUid
      if (!isOwnProfile) { next(); return }

      const profileRef = profilesCollection.doc(to.params.userUid.toString())
      const profileDoc = await profileRef.get()

      // User is owner and profile doesn´t exist, create it.
      if (currentUser && isOwnProfile && !profileDoc?.exists) {
        console.log('own profile not found, creating...')
        const newProfile = newUserProfile(currentUser)
        await profileRef.set(newProfile)
        next()
        return
      }

      if (!profileDoc?.exists) {
        console.log('public profile not found, 404...')
        router.replace('/404')
      } else {
        next()
      }
    }
  }
]
routes.forEach(router.addRoute)

router.beforeEach((to, from, next) => {
  // 404 hook
  const { matched } = to
  if (matched.length < 1) {
    router.replace('/404')
    return
  }

  // Auth guards hook
  const toProfileIfLoggedIn = matched.some(record => record.meta.toProfileIfLoggedIn)
  const currentUid = auth?.currentUser?.uid

  if (toProfileIfLoggedIn && currentUid) {
    router.replace(`/u/${currentUid}`)
  } else {
    next()
  }
})

export default router
